	<view>
		{{content.name}}
	</view>
</template>

<script>

export default {
  data() {
    return {
		content: {}
	};
  },
  props: {
    whContent: {
      type: String,
      default: ' '
    }
  },
  mounted: function () {
    let story = this.whContent;
