    const time = setInterval(()=> {
      const text = story.substring(0, i);
      i++;
	  this.$set(this.content,"name",text)
	  this.test = this.text
      if (text.length == story.length) {
        clearInterval(time);
      }
    }, 200);
  },
  methods: {}
};
</script>
